* {
  margin: 0;
  padding: 0;
}

.conteiner {
  max-width: 1780px;
  margin: 0 auto;
  padding-left: 15px;
  padding-right: 15px;
}

.heading {
  color: rgb(32, 35, 41);
  font-weight: 900;
  font-size: 5.625rem;
  text-align: center;
  padding-top: 30px;
  padding-bottom: 30px;
}

.character_cards {
  background-color: rgb(39, 43, 51);
}

@media screen and (max-width: 600px) {
  .heading {
    font-weight: 900;
    font-size: 3.625rem;
    padding-top: 30px;
    padding-bottom: 30px;
  }
}
@media screen and (max-width: 700px) {
  .heading {
    font-weight: 900;
    font-size: 2.625rem;
  }
}/*# sourceMappingURL=App.css.map */